"use strict";
const R = require('ramda');
import edd from "easy-door-data";
import purifyStore from "./libs/purify";
import Crypto from "./libs/crypto"
import cookiStore from "./libs/cookie";

let nameSpacePool={};

function EBS(config){
    const storeTypes = {'L':'l','S':'s'};
    const dataBase = {};
    const schemes = {};
    const cache = {l:{},s:{},c:{}};
    //aes
    let aesCrypto = {};
    //, localStorage,sessionStoragecookie 
    const storeEngine= {
        l : hasApi(window.localStorage) ? window.localStorage : cookiStore,
        s : hasApi(window.sessionStorage) ? window.sessionStorage : cookiStore,
        c : cookiStore
    }
    // 
    function hasApi(storage){
        try {
            storage.setItem('EBSTEST','EBSTEST');
            var str = storage.getItem('EBSTEST');
            storage.removeItem('EBSTEST');
            return str == 'EBSTEST' ? true : false
        }catch(error){
            return false
        }
    }
    function addProps(props){
        if (!props.constructor == Object) { //object
            console.error("ERROR: STORAGE [" + dataBase.$namespace + "] addPropsToSchemes method paramater must be an instanc of Object") //
        } else {
            //,        
            for(let b in props){
                if(props[b].default && props[b].type){
                    var defaultValue = props[b].default.constructor == Function ? props[b].default():props[b].default
                    let types = props[b].type.constructor == Function ? [props[b].type] : props[b].type;
                    if(types.indexOf(defaultValue.constructor)<0){
                        console.error("ERROR:invalid default value,props:"+b+",Expected "+R.pluck("name")(types)+", got "+defaultValue.constructor.name);
                        return;
                    }
                }                
            }
            for(let i in props){
                schemes[i]={
                    type: props[i].type,//
                    default:props[i].default,//null
                    // method: R.keys(storeTypes).indexOf(props[i].method)<0? storeTypes.S : storeTypes[props[i].method], //,session
                    method:function(m){
                        return R.keys(storeTypes).indexOf(m)<0? storeTypes.S : storeTypes[m];
                    }(props[i].method),
                    once:props[i].once,
                    expireTime:props[i].expireTime,
                }
                //
                Object.defineProperty(dataBase.$data,i,{
                    configurable:true,//set  get true,
                    enumerable:true,//
                    set:function(value){
                        let types = schemes[i].type && schemes[i].type.constructor == Function ? [schemes[i].type] : schemes[i].type;
                        if(types && types.indexOf(value.constructor)<0){
                            console.error("ERROR:STORAGE [" + dataBase.$namespace + "] $data."+i+" invalid value,type check failed,Expected [" +R.pluck("name")(types)+"], got "+value.constructor.name)
                        }else{
                            let cacheData = cache[schemes[i].method];
                            cacheData[i] = {
                                v: value,
                                m: schemes[i].method,
                                t: Math.round(Date.now() / 1000) //
                            }
                            setCache(schemes[i].method,cacheData)
                        }
                    },
                    get:function(){
                        let now = Math.round(Date.now() / 1000);
                        let cacheData = cache[schemes[i].method];
                        if(cacheData.constructor == Object && cacheData[i] && (schemes[i].expireTime == null || now < schemes[i].expireTime + cacheData[i].t)){
                            var data = cacheData[i];
                            if(schemes[i].once){
                               delete cache[schemes[i].method][i];
                               setCache(schemes[i].method, cache[schemes[i].method]);
                            }
                            let types = schemes[i].type && schemes[i].type.constructor == Function ? [schemes[i].type] : schemes[i].type;
                            //
                            if(types && types.indexOf(data.v.constructor)>-1){
                                return data.v
                            }else{
                                return schemes[i].default && schemes[i].default.constructor == Function ? schemes[i].default(): schemes[i].default
                            }                           
                        }else{
                            return schemes[i].default && schemes[i].default.constructor == Function ? schemes[i].default(): schemes[i].default
                        }                   
                    }
                })
            }
            Object.preventExtensions(dataBase.$data)
        }
    }
    //
    function getCache(engineNameStr){
        return aesCrypto.deCryptoData(storeEngine[engineNameStr].getItem(dataBase.$namespace));
    }
    //
    function setCache(engineNameStr,data){
        storeEngine[engineNameStr].setItem(dataBase.$namespace,aesCrypto.enCryptoData(data));
    }
    //  
    function clearProp(prop){
        delete cache.l[prop];
        setCache('l',cache.l);
        delete cache.s[prop];
        setCache('s',cache.s)
        delete cache.c[prop];
        setCache('c',cache.c)
    }
    //
    function clear(type){
        // var type = type || 'SELF';
        let clearType = ['SELF','EBS','ALL'];        
        type = clearType.indexOf(type && type.toUpperCase())<0? clearType[0] : type;
        dataBase.$data={};
        cache.l = {};
        cache.s = {};
        switch(type){
            case 'SELF':                             
                storeEngine.l.removeItem(dataBase.$namespace)
                storeEngine.s.removeItem(dataBase.$namespace)
                storeEngine.c.removeItem(dataBase.$namespace)
            break;
            case 'EBS':                  
                for(var i in storeEngine.l){
                    if(i.split(':')[0] == 'EBS'){
                        storeEngine.l.removeItem(i)
                    }
                }
                for(var i in storeEngine.s){
                    if(i.split(':')[0] == 'EBS'){
                        storeEngine.s.removeItem(i)
                    }
                }
                //cookiecookie
                for(var i in storeEngine.c.getItem()){
                    if(i.split(':')[0] == 'EBS'){
                        storeEngine.c.removeItem(i)
                    }
                }
            break;
            case 'ALL':
                storeEngine.l.clear();
                storeEngine.s.clear();
                for(var i in storeEngine.c.getItem()){
                    storeEngine.c.removeItem(i)
                }
            break;
        }
    }
    //config 
    if(!edd(config,{type:Object},"Constructor parameter [config]")){
        return {}
    }else if(!edd(config.namespace,{type:String},"Constructor parameter config.namespace") || !edd(config.props,{type:Object},"Constructor parameter config.props")){
        //config.namespace  config.props
        return {}
    }else if(R.keys(nameSpacePool).indexOf("EBS:"+config.namespace.toUpperCase())>-1){
        return {}
    }
    aesCrypto = new Crypto(config.key)
    /*
        * dataBase
        * $namespace  
        * $data 
        * addProps  
        * removeProp  
        * clear 'SELF','EBS','ALL'
    */
    Object.defineProperties(dataBase,{
        $namespace:{writable:false,configurable:false,enumerable:false,value:"EBS:"+config.namespace.toUpperCase()},
        $data:{writable:true,configurable:false,enumerable:false,value:{}},                       
        clearProp:{writable:false,configurable:false,enumerable:false,value:clearProp},
        clearData:{writable:false,configurable:false,enumerable:false,value:clear},
    })
    //schemes  $data,purifyStorepurifyStore
    addProps(config.props);
    //,schemes
    var outData = purifyStore([getCache('l'),getCache('s'),getCache('c')],schemes)
    cache.l=outData.l;
    setCache('l',cache.l)
    cache.s=outData.s;
    setCache('s',cache.s)
    cache.c=outData.c;
    setCache('c',cache.c)   
    //
    nameSpacePool[dataBase.$namespace] = dataBase;
    Object.preventExtensions(dataBase)
    return dataBase      
}
export default EBS